@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400;700&family=Merienda+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
footer,
header,
nav,
section {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
figcaption,
figure,
main {
  display: block;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
pre {
  font-family: monospace, monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}
b,
strong {
  font-weight: inherit;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
dfn {
  font-style: italic;
}
mark {
  background-color: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
audio,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
img {
  border-style: none;
}
svg:not(:root) {
  overflow: hidden;
}
button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
progress {
  display: inline-block;
  vertical-align: baseline;
}
textarea {
  overflow: auto;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
details,
menu {
  display: block;
}
summary {
  display: list-item;
}
canvas {
  display: inline-block;
}
template {
  display: none;
}
[hidden] {
  display: none;
}

html {
  font-size: 62.5%;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.6rem;
  color: #1b2456;
}

h1 {
  font-family: "Merienda One", cursive;
}

h2 {
  font-weight: bolder;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}

.landingPage {
  background-image: url(/book-club-reads/static/media/robert-anasch-McX3XuJRsUM-unsplash.8bdbbee0.jpg);
  background-blend-mode: overlay;
  background-color: rgba(0, 0, 0, 0.6);
  height: 80vh;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 0;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
}

.iconContainer {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 30px;
  right: 30px;
}

.landingPage i {
  font-size: 3rem;
  color: #fff;
  margin: 15px 0;
}


.landingPage h2 {
  font-family: "Merienda One", cursive;
  font-size: 3rem;
  width: 80%;
}

@media (max-width: 570px) {
  .landingPage h2 {
    font-size: 2.2rem;
    line-height: 5rem;
    width: 60%;
  }
}

header {
  background-color: #fff;
}

.headerNav {
  display: flex;
  list-style: none;
  text-transform: uppercase;
  height: 15vh;
  align-items: center;
}

.capitalB {
  color: white;
  font-size: 3.5rem;
  position: relative;
  z-index: 33;
}

.bookIcon {
  position: absolute;
  z-index: 0;
  font-size: 5.5rem;
  left: -36px;
  bottom: 10px;
}

.headerNav li {
  padding: 10px;
}

.navLinks {
  text-decoration: none;
  color: #1b2456;
  font-weight: 700;
  padding: 10px;
  border-radius: 10px;
  display: block;
}

.navLinks:hover,
.navLinks:focus {
  background-color: #1b2456;
  color: white;
}

.logo {
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  left: 30px;
}

h1 {
  font-size: 3rem;
  color: #1b2456;
}

.flexContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.container {
  margin: 0 auto;
  max-width: 1280px;
  width: 90%;
}

.wrapperContainer {
  margin: 0 100px;
}

.searchButton {
  background-color: #1b2456;
  color: #fff;
  border: 1px solid #1b2456;
  padding: 5px;
  height: 30px;
  width: 30px;
}

button:hover,
button:focus {
  background-color: #fff;
  color: #1b2456;
}

.searchBook {
  width: 300px;
  height: 30px;
  padding-left: 10px;
  border: 1px solid #1b2456;
}

.titleFormContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
}

form {
  position: relative;
}

.suggestionContainer {
  position: absolute;
  z-index: 1;
  display: flex;
  flex-direction: column;
  left: 0px;
  width: 300px;
  background-color: whitesmoke;
  border: 1px solid #1b2456;
  justify-content: space-evenly;
}

.suggestionContainer label {
  background-color: none;
  padding: 20px 12px;
  width: 100%;
  display: inline-block;
}

.individualSuggestion p {
  background-color: whitesmoke;
  padding: 10px;
  color: #1b2456;
}

.individualSuggestion p:hover,
.individualSuggestion p:focus {
  background-color: #c8e0fb;
}

.dropDownLink {
  width: 100%;
  display: inline-block;
}

.dropDownLink:focus {
  border: 3px solid #1b2456;
  background-color: #c8e0fb;
}

.individualSuggestion a {
  text-decoration: none;
}

.languageContainer {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10;
}

@media (max-width: 1067px) {
  h1 {
    font-size: 2rem;
  }
  .searchBook,
  .suggestionContainer {
    width: 250px;
  }
}

@media (max-width: 848px) {
  h1 {
    font-size: 2.5rem;
  }
  .flexContainer {
    flex-direction: column;
    padding: 0;
  }
  .titleFormContainer {
    flex-direction: column;
    grid-gap: 0;
    gap: 0;
    width: 100%;
  }
  .headerNav {
    height: 10vh;
  }
  .iconContainer {
    flex-direction: row;
  }
  .iconContainer i {
    margin: 0 15px;
  }
  .logo {
    left: 20px;
  }
}

@media (max-width: 570px) {
  h1 {
    font-size: 2rem;
  }
  .iconContainer {
    right: calc(50% - 114px);
  }

  .searchBook,
  .suggestionContainer {
    width: 200px;
  }

  footer {
    font-size: 1.5rem;
  }
}

.search-form {
  display: flex;
}

.carouselBookshelf,
.gridBookshelf {
  background-image: url(/book-club-reads/static/media/robert-anasch-McX3XuJRsUM-unsplash-blackandwhite.503807a7.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: #fff;
  padding: 50px 0;
  position: relative;
}

.gridBookshelf {
  justify-content: space-evenly;
}

.carouselBookshelf {
  height: 80vh;
  justify-content: space-between;
}

.carouselBookshelf i {
  font-size: 5rem;
  cursor: pointer;
  color: #1b2456;
  padding: 0 20px;
}

.carousel {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gridDisplay,
.gridDisplay .bookShelfDisplay {
  display: flex;
  flex-flow: row wrap;
  height: auto;
  margin: 0 auto;
}

.gridDisplay .bookShelfDisplay {
  flex-flow: row wrap;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
}

.gridDisplay .displayedBook {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 180px;
  background-color: rgba(245, 245, 245, 0.714);
  border-radius: 10px;
  padding: 10px;
}

.gridDisplay .displayedBook img {
  width: 100%;
  box-shadow: black 5px 5px 10px;
}

.dashboardContainer {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  height: 30px;
  width: 100%;
  justify-content: space-between;
  padding-right: 25px;
  background-color: white;
}

.gridDisplayButton {
  width: 60px;
  border: none;
  background-color: rgba(0, 0, 0, 0);
}

.gridDisplayButton i {
  font-size: 3rem;
}

.carousel .bookShelfDisplay {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  background-color: rgba(255, 255, 255, 0.595);
  padding: 25px 0;
  height: 400px;
}

.carousel .displayedBook img {
  width: 200px;
  box-shadow: black 15px 15px 20px;
}

.carousel .shelvedBooks img {
  width: 100px;
  box-shadow: black 5px 5px 10px;
}

.carousel .shelvedBooks,
.carousel .displayedBook {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.addBook,
.removeBook {
  width: 150px;
  background-color: #1b2456;
  color: #fff;
  border: 1px solid #1b2456;
  border-radius: 20px;
  padding: 10px;
  margin-top: 20px;
  text-transform: uppercase;
  font-size: 2rem;
  box-shadow: 5px 5px 10px black;
}

.chevronButton {
  border-radius: 15px;
  border: 2px solid #fff;
  background-color: #fff;
}

.chevronButton:hover,
.chevronButton:focus {
  background-color: #1b2456;
  border: 2px solid #1b2456;
}

.chevronButton:hover i,
.chevronButton:focus i {
  color: #fff;
}

.bookshelfEmptyMessage {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 80vh;
  background-color: rgba(255, 255, 255, 0.595);
}

/* Media Queries */
@media (max-width: 841px) {
  .dashboardContainer {
    padding-right: 0;
  }
}

footer {
    background-color: #1b2456;
    width: 100%;
    text-align: center;
    padding: 15px 0;
    height: 5vh;
}
footer a {
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
}
.resultBox {
  color: #fff;
  display: flex;
  flex-direction: column;
  padding: 15px;
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: overlay;
  background-color: rgba(0, 0, 0, 0.6);
  justify-content: space-between;
  border-radius: 10px;
  box-shadow: black 5px 5px 10px;
}

.resultBox img {
  align-self: center;
  box-shadow: black 5px 5px 10px;
}

.searchResSection {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

h2 {
  margin: 10px 0;
  text-transform: uppercase;
}

.descriptionContainer {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.buttonContainer {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  text-align: left;
  align-items: center;
  width: 100%;
}

.buttonContainer button {
  width: 250px;
}


.buttonContainer button,
.paginationButtonContainer button {
  background-color: #fff;
  color: #1b2456;
  text-align: left;
  margin-bottom: 15px;
  padding: 10px 0;
  border-radius: 10px;
  border: 2px solid #1b2456;
  text-transform: uppercase;
  box-shadow: black 5px 5px 10px;
}

.buttonContainer button:hover,
.buttonContainer button:focus,
.paginationButtonContainer button:hover,
.paginationButtonContainer button:focus {
  background-color: #1b2456;
  color: #fff;
}

.buttonContainer i {
  padding-left: 5px;
}

.paginationButtonContainer {
  display: flex;
  width: 80%;
  justify-content: center;
  margin: 15px auto 0;
}

.paginationButtonContainer button {
  width: 20%;
  text-align: center;
  margin: 20px 10px;
}

/* Media Queries */
@media (max-width: 850px) {
  .searchResSection {
  grid-template-columns: 1fr 1fr;
  }

  .paginationButtonContainer button {
  width: 35%;
  }
}


@media (max-width: 600px) {
  .searchResSection {
  grid-template-columns: 1fr;
}
}

.detailsFlexContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: whitesmoke;
  padding: 3rem;
  border-radius: 10px;
}

.bold {
  font-weight: 700;
}

.description span {
  font-weight: 400;
}

.exitButton {
  position: absolute;
  left: 1.8rem;
  top: 1.8rem;
  border: none;
  background-color: whitesmoke;
  border-radius: 50%;
}

.exitButton i {
  font-size: 3.5rem;
  color: #1b2456;
}

.imageContainer {
  display: flex;
  height: 100%;
  width: 200px;
  justify-content: center;
}

.imageContainer img {
  width: 500px;
  height: auto;
  box-shadow: 5px 5px 10px black;
}

.detailsFlexContainer .description {
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.detailsFlexContainer h2 {
  font-weight: bolder;
}

.lastRow {
  margin-bottom: 7.5px;
}

.checkbox {
  margin-top: 15px;
  font-size: 2rem;
  color: #1b2456;
}

.checkbox input {
  width: 25px;
  height: 25px;
  margin-right: 5px;
  color: #1b2456;
}



